                  if(arrLine[0]=='HOMBRE')elemento[1]='HOMBRE';
                  if(arrLine[0]=='MUJER')elemento[1]='MUJER';

//NIÃA
                  if(this.$store.state.database[i].AREA.includes(elemento[1])){
                     arrResultadox.push(this.$store.state.database[i]);
              }
              }
           }
           }

         }

          /*var arrResultadox = this.$store.state.database.filter(function(item) {
            for (var key in filter) {
              if (item[key] === undefined || item[key] != filter[key])
                return false;
            }
            return true;
          });*/

          var importe=0.0;
          var cantidad=0;
          for(var i=0;i<arrResultadox.length;i++){
            try {
              importe+= isNaN(parseFloat(arrResultadox[i].IMPORTE.replace(',','').replace('$',''))) ? 0 : parseFloat(arrResultadox[i].IMPORTE.replace(',','').replace('$',''));
            } catch (error) {
              importe+= isNaN(parseFloat(arrResultadox[i][" IMPORTE "].replace(',','').replace('$',''))) ? 0 : parseFloat(arrResultadox[i][" IMPORTE "].replace(',','').replace('$',''));
            }

            cantidad+= isNaN( parseFloat(arrResultadox[i].CANTIDAD.replace(',','').replace('$',''))) ? 0 : parseFloat(arrResultadox[i].CANTIDAD.replace(',','').replace('$',''));  
           
    
          }

          var promedio=0.0;
          var arrResultado=[];

          if(importe==0.0){
             arrResultado=[parseFloat(cantidad),importe];
          }else{
            promedio = parseFloat(parseFloat(importe)/parseFloat(cantidad)).toFixed(2);
             arrResultado=[parseFloat(cantidad),promedio];
          }

         console.log(arrResultadox);
          return arrResultado;
        },
        calcularPromedios(){

          if(this.$store.state.database.length<=0){
            this.snackbar = true;
                this.msgSanck="La base de datos no ha sido cargada";
                this.colorSnack='red';
                this.overlay=false;
               return;
