                this.msgSanck="Seleccione un almacen";
                this.colorSnack='red';
                this.overlay=false;
                this.loading=false;
               return;
          }

          this.workListCargada.forEach(element => {
             var arrResultado = this.searchDB(element);
              element[3]=arrResultado[0];
              element[4]=arrResultado[1];
          });

          document.getElementById('spreadsheet').jexcel.setData(this.workListCargada);


          this.snackbar=true;
          this.colorSnack='green';
          this.msgSanck='El proceso de cálculo de promedios y cantidades ha terminado';


          this.overlay = false;
          this.loading=false;
        }
